  word-wrap: break-word;
  display: block;
  cursor: pointer;
  user-select: none;
  color: #461f02;
  padding-top: 10px;
  padding-left: 40px;
  padding-bottom: 20px;
  border: 1px dashed #f0e4ca;
  padding-right: 15px;
  box-sizing: border-box;
}
.delivery__text {
  position: relative;
}
.delivery__label:hover {
  background-color: #faf8f3;
}
.delivery__radio:checked + .delivery__label {
  background-color: #fdfcfa;
  border: 1px solid #f0e4ca;
}
.delivery__radio:checked + .delivery__label .delivery__text::before,
.delivery__radio:not(:checked) + .delivery__label .delivery__text::before {
  content: "";
  position: absolute;
  display: block;
  left: -30px;
  top: 0;
  width: 20px;
  height: 20px;
  border: 1px solid #c6b9b3;
  border-radius: 100%;
  background: #ffffff;
  background-repeat: no-repeat;
  background-position: 0 0;
}
.delivery__radio:checked + .delivery__label .delivery__text::after {
  content: "";
  width: 12px;
  height: 12px;
  background: #ff6900;
  position: absolute;
  top: 15%;
  left: -25.5px;
  border-radius: 100%;
}
.delivery__radio:checked + .delivery__label--pickup .delivery__text::after {
  top: 26%;
}
.delivery__notice {
  display: block;
  color: #c4b7b1;
  margin-top: 5px;
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .delivery__label {
    width: 708px;
